@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);

.navbar{background-color:#060b26;height:80px;display:flex;justify-content:flex-start;align-items:center}.menu-bars{margin-left:2rem;font-size:2rem;background:none}.nav-menu{background-color:#060b26;width:100vh;display:flex;justify-content:center;position:fixed;top:0;left:-100%;transition:850ms}.nav-menu.active{left:0;transition:350ms}.nav-text{display:flex;justify-content:flex-start;align-items:center;padding:8px 0px 8px 16px;list-style:none;height:60px}.nav-text .nav-text a{text-decoration:none;color:#f5f5f5;font-size:18px;width:95%;height:100%;display:flex;align-items:center;padding:0 16px;border-radius:4px}.nav-text .nav-text a .nav-text a:hover{background-color:#1a83ff}.navbar-items{width:100%}.navbar-toggle{background-color:#060b26;width:100%;height:80px;display:flex;justify-content:flex-start;align-items:center}span{margin-left:16px}.auth{padding-left:180vh;color:white}

*{box-sizing:border-box;margin:0;padding:0;font-family:'Lato', sans-serif}.home,.reports,.products{display:flex;height:90vh;align-items:center;justify-content:center;font-size:3rem}a{color:white;text-decoration:none}

